@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

/* App Default */
a {
  color: #fafafc;
  cursor: pointer;
}
a:hover {
  color: #ffc107;
}
li {
  list-style-type: none;
  margin-bottom: 8px;
}
span,
cite {
  display: block;
  font-size: 13px;
}
hr {
  width: calc(100% - 50px);
  border-color: #737378;
}
svg {
  display: block;
  width: 100%;
}
span {
  color: #fafafc;
}
cite {
  color: #737378;
  font-style: initial;
}
strong {
  color: #ffc107;
}
h1, h2, h3 {
  color: #fafafc;
  -webkit-transition: .4s ease-in-out;
  transition: .4s ease-in-out;
  font-size: 14px;
  font-weight: 600;
  margin: 10px 0px;
  cursor: pointer;
}
h1:hover, h2:hover, h3:hover {
  color: #ffc107;
}

/* Float */
.left {
  float: left;
}
.right {
  float: right;
}

/* Main columns */
.wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  max-width: 1380px;
  width: 100%;
  margin: 0 auto;
}
.left-sidebar {
background: #20202a;
  width: 290px;
  min-width: 290px;
  z-index: 999;
  -webkit-box-shadow: 0 3px 8px 0 rgb(15 15 20 / 20%);
  box-shadow: 0 3px 8px 0 rgb(15 15 20 / 20%);
  padding-top: 240px;
  float: left;
}
.left-sidebar section {
  padding: 15px 30px 10px;
}

.right-sidebar {
  width: 80px;
  background: #20202a;
  float: right;
}

main {
  width: calc(100% - 370px);
  padding: 15px;
}
main section {
  margin-bottom: 35px;
}

.sidebar-about-me {
  padding: 30px;
  height: 255px;
  background: #252531;
  text-align: center;
  box-shadow: 0 1px 4px 0 rgb(15 15 20 / 10%);
  position: fixed;
  width: 290px;
  top: 0px;
  z-index: 10;
}
.sidebar-about-me:after {
  content: '';
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 0;
  border: 25px solid transparent;
  border-bottom: 12px solid #20202a;
  z-index: 2;
}
.sidebar-about-me img {
-o-object-fit: cover;
object-fit: cover;
border-radius: 50%;
}
.sidebar-about-me cite {
margin-bottom: 4px;
}

.sidebar-info ul li {
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-pack: justify;
-ms-flex-pack: justify;
justify-content: space-between;
}
.sidebar-languages ul li {
  float: left;
  width: 29%;
  margin-right: 6.3%;
  text-align: center;
  padding: 0px 1%;
  position: relative;
}
.sidebar-languages ul li:last-child {
  margin-right: 0px;
}

.sidebar-languages ul li span {
  margin: 10px 0px;
  display: block;
}
.sidebar-languages ul li cite {
  position: absolute;
  top: 21px;
  left: calc(50% - 12px);
  font-size: 10px;
}

.sidebar-print cite {
  text-shadow: 0 0 1px rgb(250 250 252 / 40%);
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-weight: 600;
}

.sidebar-knowledge li {
  color: #737378;
  margin-bottom: 5px;
}

.sidebar-knowledge ul {
  padding-left: 5px;
}
.sidebar-knowledge li:before {
  content: "\00BB";
  margin-right: 5px;
}

.sidebar-coding svg {
  height: 12px;
}
.sidebar-coding span {
  float: left;
}
.sidebar-coding cite {
  float: right;
}

.sidebar-print cite:hover {
  color: #ffc107;
}

.social-icons {
  padding-top: 5px;
  position: relative;
  z-index: 15;
}
.social-icons a {
  margin-right: 10px;
}
.social-icons a:last-child {
  margin-right: 0px;
}
.social-icons a i {
  font-size: 21px;
  color: #737378;
}
.social-icons a:hover i {
  color: #fafafc;
}

main h2 {
  font-size: 18px;
}
.main-featured {
  background: url(https://wallpapercave.com/wp/h2lTSTb.jpg);
  background-repeat: no-repeat;
  transform: scale(1);
  opacity: 1;
  background-size: cover;
  background-position: center;
  position: relative;
  box-shadow: 0 3px 8px 0 rgb(15 15 20 / 20%);
  z-index: 99;
  margin-bottom: 15px;
}
.main-featured-overlay {
  background-image: -webkit-gradient(linear,left top,right top,color-stop(15%,rgba(45,45,58,.9)),color-stop(50%,rgba(45,45,58,.7)),to(rgba(43,43,53,.7)));
  background-image: linear-gradient( 
90deg,rgba(45,45,58,.9) 15%,rgba(45,45,58,.7) 50%,rgba(43,43,53,.7) 100%);
  height: 275px;
  padding: 60px;
}
.main-featured span {
  font-size: 55px;
  font-weight: bold;
}
.main-featured .code-text {
  color: #fafafc;
  font-size: 18px;
  font-weight: 400;
}
.main-featured .code-text strong {
  font-weight: 400;
}
.featured-text {
  display: inline;
}
.main-featured a {
  background: #ffc107;
  color: #1e1e28;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  padding: 10px 25px;
  margin-top: 25px;
  display: inline-block;
  font-weight: bold;
}
.main-featured a:hover {
  -webkit-transform: translateY(-2px);
  transform: translateY(-2px);
  color: #1e1e28;
}

.main-services ul li {
  padding: 15px;
  float: left;
  width: calc(33.333% - 7px);
  background: #252531;
  margin-right: 10px;
}
.main-services ul li:nth-child(3n) {
  margin-right: 0px;
}
.main-services ul li div {
  display: flex;
  justify-content: space-between;
  margin-bottom: 4px;
}
.main-services ul li div cite  {
  color: #ffc107;
}

.main-services p,
.main-projects p {
  color: #737378;
  font-weight: 400;
  font-size: 13px;
  line-height: 20px;
  letter-spacing: 0.25px;
  margin: 15px 0px;
}

.divider {
  padding: 15px;
  background: #252531;
  cursor: pointer;
  text-align: center;
  font-size: 16px;
  color: #fafafc;
}
.divider:hover {
  color: #ffc107;
}

.main-projects li {
  float: left;
  width: calc(50% - 5px);
  background: #252531;
  padding: 15px;
  position: relative;
  margin-right: 5px;
  margin-bottom: 30px;
}
.main-projects li:nth-child(2n) {
  margin-left: 5px;
  margin-right: 0px;
}
.main-projects ul li span {
  font-size: 15px;
}
.main-projects li cite a {
  color: #737378;
  font-style: italic;
  font-weight: 300;
  font-size: 11px;
}
.main-projects .visit-project {
  background: #191923;
  padding: 5px 15px;
  color: #ffc107;
  border-radius: 10%;
}
.main-projects .visit-project:hover {
  color: #fafafc;
}
.main-projects ul li img {
  position: absolute;
  right: 20px;
  top: -15px;
  border-radius: 50%;
  border: 1px solid #252531;
  width: 60px;
  height: 60px;
}

.main-footer {
  display: flex;
  justify-content: space-between;
  background: #252531;
  padding: 10px 15px;
  margin-bottom: 0px;
}
.main-footer  .main-footer-email cite {
  display: inline;
  margin-right: 5px;
}
.main-statistics ul li {
  float: left;
  width: 50%;
  text-align: center;
}

.main-statistics ul li strong {
  font-size: 35px;
}

.main-statistics ul li span {
  font-size: 16px;
}

.social-menu-icons {
  position: fixed;
  width: 80px;
  padding: 15px 0px;
}
.social-menu-icons a {
  text-align: center;
  display: block;
  margin-bottom: 5px;
}
.social-menu-icons a i {
  cursor: pointer;
  font-size: 30px;
  color: #737378;
}
.social-menu-icons a:hover i {
  color: #fafafc;
}